export default class Brush {
    constructor(options) {
        this.active = false;
        this.color = "red";
        this.size = 5;
        this.capStyle = "round";
        this.fillPolygons = true;
        this.canvas = options.canvas;
        this.context = options.canvas.context;
        if (options.color)
            this.color = options.color;
        if (options.size)
            this.size = options.size;
        if (options.capStyle)
            this.capStyle = options.capStyle;
        if (options.fillPolygons)
            this.fillPolygons = options.fillPolygons;
        if (!options.nolisteners)
            this.setupListeners();
    }
    setupListeners() {
        let el = this.canvas.el;
        el.addEventListener("mousedown", e => this.begin.apply(this, [e.clientX, e.clientY]));
        el.addEventListener("mousemove", e => this.move.apply(this, [e.clientX, e.clientY]));
        el.addEventListener("mouseup", e => this.end.apply(this, [e.clientX, e.clientY]));
    }
    begin(x, y) {
        if (this.active)
            return;
        this.active = true;
        let context = this.context;
        context.lineWidth = this.size;
        context.strokeStyle = this.color;
        context.fillStyle = this.color;
        context.lineCap = this.capStyle;
        context.beginPath();
        context.moveTo(x, y);
        context.lineTo(x, y);
        context.stroke();
    }
    move(x, y) {
        if (!this.active)
            return;
        let context = this.context;
        context.lineTo(x, y);
        context.stroke();
    }
    end(x, y) {
        if (!this.active)
            return;
        let context = this.context;
        context.lineTo(x, y);
        context.stroke();
        context.closePath();
        if (this.fillPolygons)
            context.fill();
        this.active = false;
        this.canvas.save();
    }
}
